
.lodash {
  width: 100%;
  min-height: 30rem;
  background: #eee;
  box-sizing: border-box;
  overflow: hidden;
  margin: 0 auto;
  padding: 2rem;
}
.lodash .content h1 {
  margin: 1rem 0 0.8rem 0;
  font-size: 3rem;
}
.lodash .content h2 {
  margin: 0.8rem 0 0.4rem 0;
}
.lodash .content code {
  margin: 0.5rem 0;
}


